import React from "react";
import { HiMinusSm, HiPlusSm } from "react-icons/hi";
import { MdCancel, MdShoppingCart } from "react-icons/md";

export default function Product({
  isCart = false, item, currency, addQuantity, decreaseQuantity, removeItem
}) {
  return (

    <div className="cart-item">
      <div>
        <img
          src={item.image}
          className="cart-image"
          alt=""
        ></img>
      </div>
      <div className="cart-data">
        <div className="cart-data-top">
          <div>
            <p
              style={{
                fontWeight: 700,
                margin: 0,
                fontSize: 17,
                lineHeight: "22px",
              }}
            >
              {item.name}
            </p>
            <p style={{ margin: 0, marginTop: 5, fontSize: 13, color: "gray" }}>
              Save  {currency}{parseInt(item.selling_price) < item.mark_price && Math.round((item.mark_price - parseInt(item.selling_price)))}
            </p>
          </div>
          <div>
            <MdCancel onClick={removeItem} size={20} />
          </div>
        </div>
        <div className="cart-data-bottom">
          <h5 className="cart-price">
            {currency}{item.selling_price}
            {(item.mark_price > item.selling_price) && <s style={{ fontSize: 11, fontWeight: 400 }}>{item.mark_price}</s>}
          </h5>
          {isCart ?
            <div className="cart-quantity">
              <HiPlusSm
                onClick={addQuantity}
                className="cart-select cart-add"
                style={{ color: "white" }}
              />
              {item.quantity}
              <HiMinusSm
                onClick={decreaseQuantity}
                className="cart-select cart-add"
                style={{ color: "white" }}
              />
            </div> :
            <div className="add">
              <MdShoppingCart style={{ color: 'white' }} size={15} />

            </div>
          }
        </div>
      </div>
    </div>

  );
}
