import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router'
import TopItems from './TopItems'
import { IoMdArrowRoundBack } from "react-icons/io";
import api from './Api'
import InfiniteScroll from 'react-infinite-scroll-component';
import Product from "./ProductGrid"
import { Context } from './Context';
import Constants from '../Constant'
import { FaSearch } from 'react-icons/fa';
export default function CategoryShop() {
    const context = React.useContext(Context)
    const history = useHistory()
    const [title, setTitle] = useState('Shop By Category')
    const [products, setProducts] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(0);
    const [totalProducts, setTotalProducts] = useState(15);

    const loadProducts = (page = 1) => {
        api.get('/products/category/' + window.location.pathname.split("/").pop() + '?page=' + page)
            .then(res => {
                setCurrentPage(res.pagination.current_page);
                setTotalPages(res.pagination.total_pages);
                setTotalProducts(res.pagination.total);
                return res.data.map(product => ({ ...product, images: JSON.parse(product.images).map(image => `${Constants.IMAGE_BASE}${image}`) }))
            }).then(data => {
                setProducts([...products, ...data])
                if (data.length > 0) setTitle('Category: ' + data[0].category_name)
                else setTitle("No Item Found.")
            });
    }
    useEffect(() => loadProducts()
        // eslint-disable-next-line 
        , [])
    return (
        <div className="home-main">
            <TopItems right={<FaSearch size={25} onClick={e => history.push("/search")} />} left={<IoMdArrowRoundBack size={25} onClick={() => history.goBack()} />} title={title} />
            <hr />
            <InfiniteScroll loader={<b>fetching..</b>} dataLength={totalProducts} className='products' hasMore={currentPage < totalPages} next={() => loadProducts(currentPage + 1)}>

                {products.map((product) => <Product click={() => history.push("/product-info/" + product.id)} currency={context.state.currency} history={history} isInCart={context.state.cart.some(x => x.id === product.id)} addToCart={context.addToCart} secondary_color={context.state.secondary_color} data={product} key={product.id} />)}

            </InfiniteScroll>
        </div>
    )
}
