import React, { useContext, useEffect } from "react";
import { MdNotifications, MdRemoveShoppingCart } from "react-icons/md";
import { RiHeart2Fill, RiSecurePaymentFill } from 'react-icons/ri'
import { useHistory } from "react-router";
import Product from "./Product"
import { Context } from './Context'
import TopItems from "./TopItems";
import NothingFound from './NothingFound'
export default function Cart() {
  const context = useContext(Context)
  const history = useHistory();
  useEffect(() => {
    document.title = "Cart"
  }, [])
  return (
    <div className="home-main">
      <TopItems
        right={<RiHeart2Fill size={25} onClick={() => history.push("/wishlist")} />}
        title="My Cart"
        left={<MdNotifications onClick={() => history.push("/notification")} size={25} />}
      />


      <div className="cart-list">
        {context.state.cart.map((item) =>
          <Product currency={context.state.currency} removeItem={() => context.decreaseQuantity(item, true)} addQuantity={() => context.addToCart(item)} decreaseQuantity={() => context.decreaseQuantity(item)} isCart='true' key={item.id} item={item} />
        )}
        {context.state.cart.length === 0 && <NothingFound image={<MdRemoveShoppingCart size={35}/>} data="Add a item in cart and it will be here."  />}








        {context.state.cart.length > 0 && <div className="cart-total-price-div">
          <div className="cart-total-price">
            <h6>Total Price</h6>
            <h1>{context.state.currency}{context.state.cart.reduce((a, b) => a + (b.selling_price * b.quantity), 0)}</h1>

          </div>
          <div className="cart-pay" onClick={() => history.push("/checkout")}>
            <RiSecurePaymentFill />&nbsp;
            <h6 style={{ margin: "0px" }}>Proceed to Pay</h6>
          </div>
        </div>}



      </div>
    </div>
  );
}
