import React, { useEffect } from "react";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdDelete } from "react-icons/md";
import { useHistory } from "react-router";
import api from './Api';
import NothingFound from './NothingFound';
export default function WishList() {
  const history = useHistory();
  const [notifications, setNotifications] = React.useState([]);
  useEffect(() => {
    api.get('/notifications').then(res => {
      setNotifications(res.data);
    })
  }, []);
  return (
    <div className="home-main">
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>Notifications</h4>
        </div>
        <MdDelete style={{ color: "black", visibility: "hidden" }} />
      </div>

      <div className="cart-list">
        {notifications.length > 0 ? notifications.map(notification =>
          <div className="cart-item">
            <div className="notification-div">
              Your Order "1a24f6af1a3f64" has been shipped and will arrive soon at your destination.
              <p style={{ margin: 0, fontSize: 10, color: "gray" }}>
                Oct 26, 2021, 11:11 AM
              </p>
            </div>
          </div>)
          : <NothingFound data={"No notification yet"} />}

      </div>
    </div>
  );
}
