import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdDelete, MdQuestionAnswer, MdSend } from "react-icons/md";
import api from './Api'
import Question from "./Question";
import InfiniteScroll from "react-infinite-scroll-component";
import NothingFound from "./NothingFound";
import { toast } from "react-toastify";
export default function Questions() {
  const history = useHistory();
  const [questions, setQuestions] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [totalQuestions, setTotalQuestions] = useState(15);
  const [question, setQuestion] = useState("");

  const loadQuestions = (page = 1) => {
    api.get(`questions/${window.location.pathname.split('/').pop()}?page=${page}`)
      .then(res => {
        setQuestions([...questions, ...res.data]);
        setCurrentPage(res.pagination.current_page);
        setTotalPages(res.pagination.total_pages);
        setTotalQuestions(res.pagination.total);
      })
  }
  useEffect(() => loadQuestions(), 
  // eslint-disable-next-line
  []);

  return (
    <div className="home-main">
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>Question and answers</h4>
        </div>
        <MdDelete style={{ opacity: 0 }} size={25} />
      </div>
      <div className="rating-review">
        <p
          style={{
            margin: "0px 0px 10px 0px",
            fontSize: 15,
            color: "black",
            fontWeight: 500,
            position: "relative",
          }}
        >
          Questions about this product
        </p>
        <InfiniteScroll loader={<b>fetching..</b>} dataLength={totalQuestions} className='products' hasMore={currentPage < totalPages} next={() => loadQuestions(currentPage + 1)}>
          {questions.map((q) => (
            <Question key={q.id} question={q} />
          ))}


        </InfiniteScroll>
        {questions.length === 0 && <NothingFound image={<MdQuestionAnswer size="30" />} data={"No Questions yet. Be the first one to ask a question."} />}


      </div>
      <div className="review-box">
        <form onSubmit={e => {
          e.preventDefault();
          api.post("/questions", {
            question: question,
            product_id: window.location.pathname.split('/').pop()
          }).then(res =>{
            setQuestion("");
            toast.success("Question added successfully")
            loadQuestions(currentPage);
          })
            .catch(err => {
              if (err.error) toast.error(err.error)
              else if (err.errors) {
                Object.keys(err.errors).forEach(key => {
                  toast.error(err.errors[key][0])
                })
              }
            })
        }}>
          <div className="checkout-name">
            <input required value={question} onChange={e => setQuestion(e.target.value)}
              className="form-control" placeholder="Ask a question" style={{ marginBottom: "5px" }} />

            <button className="address-book-action btn">
              <MdSend size={30} />
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
