import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdDelete, MdStarBorder, MdStar, MdSend } from "react-icons/md";
import Rating from "react-rating";
import api from './Api'
import { toast } from 'react-toastify'
import Review from './Review'
import InfiniteScroll from "react-infinite-scroll-component";
import NothingFound from './NothingFound'

export default function Reviews() {
  const history = useHistory();
  const [reviews, setReviews] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [totalReviews, setTotalReviews] = useState(15);
  const [review, setReview] = useState('')
  const [rate, setRate] = useState(2)
  const [averageReview, setAverageReview] = useState(0)
  const loadReviews = (page = 1) => {
    api.get("/reviews/" + window.location.pathname.split("/").pop() + "?page=" + page).then(res => {
      setReviews(res.data);
      setCurrentPage(res.pagination.current_page);
      setAverageReview(res.average_review || 0);
      setTotalPages(res.pagination.total_pages);
      setTotalReviews(res.pagination.total);
    });
  }
  useEffect(() => loadReviews(), []);

  return (
    <div className="home-main" style={{ marginBottom: "120px" }}>
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>Reviews</h4>
        </div>
        <MdDelete style={{ opacity: 0 }} size={25} />
      </div>
      <div className="review">
        <div className="review-div">
          <p
            style={{
              margin: 0,
              fontSize: 35,
              color: "black",
              fontWeight: 500,
              position: "relative",
            }}
          >
            {averageReview}<small style={{ fontWeight: 300, fontSize: 25 }}>/5</small>
            <MdStar size={30} color={"orange"} />
          </p>
        </div>
      </div>
      <div className="rating-review" style={{ marginBottom: "20px" }}>
        <p
          style={{
            margin: 0,
            fontSize: 15,
            color: "black",
            fontWeight: 500,
            position: "relative",
          }}
        >
          Ratings and Reviews
        </p>

        <InfiniteScroll loader={<b>fetching..</b>} dataLength={totalReviews} className='products' hasMore={currentPage < totalPages} next={() => loadReviews(currentPage + 1)}>
          {reviews.map((review) => (
            <Review key={review.id} review={review} />
          ))}


        </InfiniteScroll>
        {reviews.length === 0 && <NothingFound image={<MdStar size="30" />} data={"No Reviews yet. Be the first one to review this product !"} />}





      </div>

      <div className="review-box">
        <Rating initialRating={rate} onChange={e => setRate(e)} emptySymbol={<MdStarBorder size={30} color={"orange"} />} fullSymbol={<MdStar color={"orange"} size={30} />} />

        <form onSubmit={e => {
          e.preventDefault()
          api.post('/reviews', {
            product_id: window.location.pathname.split("/").pop(),
            rate: rate,
            message: review
          }).then(res => {
            console.log(res.data)
          })
            .catch(err => {
              if (err.error) toast.error(err.error)
              else if (err.errors) {
                Object.keys(err.errors).forEach(key => {
                  toast.error(err.errors[key][0])
                })
              }
            })
        }}>
          <div
            className="checkout-name"
          >


            <input required value={review} onChange={e => setReview(e.target.value)} className="form-control" placeholder="Write a review..." style={{ marginBottom: "5px", width: '100%', flex: 1 }} />


            <button className="address-book-action btn"><MdSend size={30} /></button>
          </div>
        </form>
      </div>
    </div>
  );
}
