import React, { Component } from 'react'
import { toast } from 'react-toastify'
export const Context = React.createContext()
export default class Provider extends Component {

    state = {
        announcement: null,
        primary_color: null,
        secondary_color: 'red',
        currency: '$',
        shop_closed: null,
        icon_image: null,
        loader: null,
        cart: [],
        user: null,

    }

    reload = () => {
        let data = localStorage.getItem('configs');
        let cart = localStorage.getItem('cart');
        let user = localStorage.getItem('user');
        if (data) this.setState(JSON.parse(data))
        if (cart) this.setState({ cart: JSON.parse(cart) })
        if (user) this.setState({ user: JSON.parse(user) })
    }

    componentDidMount() {
        this.reload()
    }

    addToCart = (item) => {
        let cart = localStorage.getItem('cart')
        if (cart) cart = JSON.parse(cart)
        else cart = []
        if (cart.find(i => i.id === item.id)) {
            cart.find(i => i.id === item.id).quantity++
        } else {
            cart.push({
                id: item.id,
                name: item.name,
                image: item.images[0],
                mark_price: item.mark_price,
                selling_price: item.selling_price,
                quantity: 1,
                stock: item.stock,
            })
            toast.success(`${item.name} added to cart`)

        }
        this.setState({ cart })
        localStorage.setItem('cart', JSON.stringify(cart))
    }
    decreaseQuantity = (item, remove = false) => {
        let cart = localStorage.getItem('cart')
        if (cart) cart = JSON.parse(cart)
        else cart = []
        let itemSelected = cart.find(i => i.id === item.id)
        if (itemSelected && remove) {
            if (window.confirm('Are you sure you want to remove this item from the cart?')) {
                cart.splice(cart.indexOf(itemSelected), 1)
                toast.success(`${item.name} removed from cart`)
            }
        }
        else if (itemSelected) {
            if (itemSelected.quantity < 2) {
                if (window.confirm('Are you sure you want to delete this item from the cart?')) {
                    cart.splice(cart.findIndex(i => i.id === item.id), 1)
                }
            }
            else itemSelected.quantity--
        }
        this.setState({ cart })
        localStorage.setItem('cart', JSON.stringify(cart))
    }

    render() {
        return (
            <Context.Provider value={{
                state: this.state,
                reload: this.reload,
                addToCart: this.addToCart,
                decreaseQuantity: this.decreaseQuantity,
                setState: (d) => this.setState(d)
            }}>
                {this.props.children}
            </Context.Provider>
        )
    }
}