import { InputAdornment, TextField } from "@material-ui/core";
import React from "react";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdDelete } from "react-icons/md";
import { RiLockPasswordFill } from "react-icons/ri";
import { useHistory } from "react-router";
import api from './Api'
import { toast } from 'react-toastify'
export default function ChangePassword() {
  const history = useHistory();
  const [password, setPassword] = React.useState("");
  const [newPassword, setNewPassword] = React.useState("");
  const [confirmPassword, setConfirmPassword] = React.useState("");
  return (
    <div className="home-main">
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>Change Password</h4>
        </div>
        <MdDelete style={{ color: "black", visibility: "hidden" }} size={25} />
      </div>

      <div className="cart-list">
        <form onSubmit={e => {
          e.preventDefault();
          localStorage.setItem('skipAuthError', true)
          api.post('users/change-password', {
            old_password: password,
            new_password: newPassword,
            new_password_confirmation: confirmPassword
          }).then(res =>
            window.location = '/login?text=Password changed succeessfully.'
          ).catch(err => {
            if (err.error) toast.error(err.error)
            else if (err.errors) {
              Object.keys(err.errors).forEach(key => {
                toast.error(err.errors[key][0])
              })
            }
          })

        }}>
          <div className="checkout-details">
            <p
              style={{
                fontWeight: 500,
                marginTop: 10,
                fontSize: 18,
                lineHeight: "22px",
              }}
            >
              Enter your old password
            </p>
            <TextField
              value={password}
              required
              onChange={(e) => setPassword(e.target.value)}
              type={"password"}
              placeholder="Old password "

              style={{
                border: "1px solid black",
                backgroundColor: "white",
                borderRadius: 10,
                marginTop: "5px",
                marginBottom: "20px",
              }}
              InputProps={{
                disableUnderline: true,
                startAdornment: (
                  <InputAdornment position="start">
                    <RiLockPasswordFill style={{ margin: "8px" }} size={20} />
                  </InputAdornment>
                )
              }}
            />
            <p
              style={{
                fontWeight: 500,
                marginTop: 10,
                fontSize: 18,
                lineHeight: "22px",
              }}
            >
              Enter your new password
            </p>
            <TextField
              required
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              type={"password"}
              placeholder="New password "

              style={{
                border: "1px solid black",
                backgroundColor: "white",
                borderRadius: 10,
                marginTop: "5px",
                marginBottom: "20px",
              }}
              InputProps={{
                disableUnderline: true,
                startAdornment: (
                  <InputAdornment position="start">
                    <RiLockPasswordFill style={{ margin: "8px" }} size={20} />
                  </InputAdornment>
                )
              }}
            />
            <TextField
              required
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              type={"password"}
              placeholder="Confirm password "

              style={{
                border: "1px solid black",
                backgroundColor: "white",
                borderRadius: 10,
                marginTop: "5px",
                marginBottom: "20px",
              }}
              InputProps={{
                disableUnderline: true,
                startAdornment: (
                  <InputAdornment position="start">
                    <RiLockPasswordFill style={{ margin: "8px" }} size={20} />
                  </InputAdornment>
                )
              }}
            />
            <div style={{ display: "flex", justifyContent: "center", marginTop: "30px" }}>
              <button className="sign-in">Change Password</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
