import React, { useEffect } from "react";
import { RiHeart2Fill, RiSearch2Line } from "react-icons/ri";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField from "@material-ui/core/TextField";
import { MdNotifications } from "react-icons/md";
import { useHistory } from "react-router";
import api from "./Api";
import ProductGrid from "./ProductGrid";
import Constants from "../Constant";
import { Context } from './Context';
import TopItems from "./TopItems";
export default function Search() {
  const history = useHistory();
  const context = React.useContext(Context);
  const [search, setSearch] = React.useState("");
  const [products, setProducts] = React.useState([]);
  const searchProduct = () => {
    api.get("products/search/" + search)
      .then((res) => res.data.map(product => ({ ...product, images: JSON.parse(product.images).map(image => `${Constants.IMAGE_BASE}${image}`) })))
      .then(products => setProducts(products))

  };
  useEffect(() => {
    document.title = "Search";
  }, []);

  return (
    <div className="home-main">
      <TopItems
        right={<RiHeart2Fill size={25} onClick={() => history.push("/wishlist")} />}
        title="Search"
        left={<MdNotifications onClick={() => history.push("/notification")} size={25} />}
      />


      <div className="search-div">
        <form style={{display:'contents'}} onSubmit={e => { e.preventDefault(); searchProduct(); }}>
          <TextField
            value={search}
            fullWidth
            onChange={(e) => setSearch(e.target.value)}
            placeholder="Search "
            className="search-bar"
            style={{ backgroundColor: "white", borderRadius: 10 }}
            InputProps={{
              disableUnderline: true,
              endAdornment: (
                <InputAdornment onClick={searchProduct} position="start">
                  <RiSearch2Line style={{ margin: "8px" }} size={20} />
                </InputAdornment>
              ),
            }}
          />
        </form>

        {/* <div className="sort-button">
          <GoSettings style={{ color: "black" }} size={30} />
        </div> */}
      </div>

      <div className="products">

        {products.map((product) => <ProductGrid key={product.id} data={product} currency={context.state.currency} history={history} isInCart={context.state.cart.some(x => x.id === product.id)} addToCart={context.addToCart} />)}
      </div>
    </div>
  );
}
