import React from "react";
import { RiHeart2Fill } from "react-icons/ri";
import { MdShoppingCart, MdStar } from "react-icons/md";

export default function ProductGrid({
  data = null,
  currency,
  addToCart,
  isInCart,
  history,
  click
}) {
  return (
    <div className="product-info">
      <img
        onClick={click}
        src={
          data.images[0] ||
          "https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/2-bball-lebron-1504032768.jpg?resize=480:*"
        }
        className="product-image"
        alt=""
      />

      <div className="star-div">
        <div style={{ margin: 0, textAlign: "center" }}>
          {" "}
          {parseInt(data.selling_price) < data.mark_price && (
            <div className="discount">
              {Math.round(
                ((data.mark_price - parseInt(data.selling_price)) * 100) /
                data.mark_price
              )}
              % off
            </div>
          )}
        </div>
        <div className="star">
          <MdStar style={{ color: "orange" }} size={16} />
          {data.rating || 0}
          &nbsp;
          <RiHeart2Fill style={{ color: "orange" }} size={16} />
          {data.wish}
        </div>
      </div>
      <p  onClick={click}style={{ textAlign: "center", fontWeight: "bold", margin: "5px" }}>
        {data.name || "Luke"}
      </p>

      <div onClick={click} className="price-div">
        <h6
          style={{ textAlign: "center", margin: "5px", fontWeight: "bolder" }}
        >
          {data.mark_price > data.selling_price && (
            <s style={{ fontSize: 11, fontWeight: 400, marginRight: 4 }}>
              {currency}
              {data.mark_price}
            </s>
          )}
          {currency}
          {data.selling_price}
        </h6>
      </div>
      <div
        onClick={(e) => (isInCart ? history.push("/cart") : addToCart(data))}
        className="add-to-cart"
      >
        <div className="add">
          <b style={{ fontSize: 12 }}>
            {isInCart ? "Already in cart" : "Add to cart"}&nbsp;
          </b>

          <MdShoppingCart style={{ color: "white", fontSize: 15 }} />
        </div>
      </div>
    </div>
  );
}
