import React, { useContext, useEffect, useState } from "react";
import { RiHeart2Fill } from "react-icons/ri";
import Carousel from "react-bootstrap/Carousel";
import { useHistory } from "react-router";
import api from "./Api";
import Product from "./ProductGrid"
import { MdNotifications } from "react-icons/md";
import Constants from '../Constant'
import { Context } from './Context';
import parse from 'html-react-parser';
import InfiniteScroll from 'react-infinite-scroll-component';
export default function Home() {
  const context = useContext(Context)
  const history = useHistory();
  const [sliders, setSliders] = useState([]);
  const [categories, setCategories] = useState([]);
  const [products, setProducts] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [totalProducts, setTotalProducts] = useState(15);
  useEffect(() => {
    document.title="Home"
    api.get("sliders").then((res) => {
      setSliders(res.data);
      api.get("categories").then((res) => {
        setCategories(res.data);
        loadProducts();
      });

    });
    // eslint-disable-next-line
  }, []);
  const loadProducts = (page = 1) => {
    api.get("products?page=" + page)
      .then((res) => {

        setCurrentPage(res.pagination.current_page);
        setTotalPages(res.pagination.total_pages);
        setTotalProducts(res.pagination.total);
        return res.data.map(product => ({ ...product, images: JSON.parse(product.images).map(image => `${Constants.IMAGE_BASE}${image}`) }))
      })
      .then(data => setProducts([...products, ...data]));
  }

  return (
    <div className="home-main" >
      <div className="home-top-bar">
        <MdNotifications style={{ color: "var(--secondary-color)" }} size={25} />
        <div className="logo">
          <img
            src={Constants.IMAGE_BASE + context.state.icon_image}
            className="logo"
            alt="logo"
          ></img>
        </div>
        <RiHeart2Fill
          style={{ color: "var(--secondary-color)" }}
          size={25}
          onClick={() => history.push("/wishlist")}
        />
      </div>
      {context.state.shop_closed && <div className="alert alert-danger" style={{ margin: 5 }}>
        The shop is currently closed.
      </div>}
      {context.state.announcement && <div className="alert alert-success" style={{ margin: 5 }}>
        {parse(context.state.announcement)}
      </div>}

      <div className="crousel-div">
        <Carousel>
          {
            sliders.map(slider => <Carousel.Item key={slider.id}>
              <img
                className="d-block w-100"
                src={Constants.IMAGE_BASE + slider.image}
                alt="First slide"
              />
            </Carousel.Item>
            )
          }
        </Carousel>
      </div>
      <br />
      <h3 style={{ margin: 0 }}>Shop by category</h3>
      <div className="brands">
        {categories.map((category) => (
          <div onClick={e => history.push("/category/" + category.id)} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' , marginRight:"15px"}} key={category.id}>
            <div  className="brand-cat">
            <img
              src={Constants.IMAGE_BASE + category.image}
              className="brand-logo"
              alt="category"
            ></img>
            </div>
            <div className="brand-name">{category.name}</div>
          </div>
        ))}
      </div>

      <div>
        <h3 style={{ margin: 0, marginTop: 10 }}>Our Products</h3>

        <br />


        <InfiniteScroll loader={<b>fetcthing..</b>} dataLength={totalProducts} className='products' hasMore={currentPage < totalPages} next={() => loadProducts(currentPage + 1)}>

          {products.map((product) => <Product click={() => history.push("/product-info/" + product.id)} currency={context.state.currency} history={history} isInCart={context.state.cart.some(x => x.id === product.id)} addToCart={context.addToCart} secondary_color={context.state.secondary_color} data={product} key={product.id} />)}

        </InfiniteScroll>


      </div>
    </div >
  );
}
