import axios from 'axios';
import Constants from '../Constant'


const axiosInstance = axios.create({
    baseURL: Constants.API_URL,
});

axiosInstance.interceptors.request.use((config) => {
    try {
        if (!localStorage.getItem('skipLoading')) document.getElementById('spinner').classList.remove('hide');
    }
    catch (e) { }
    let token = localStorage.getItem('token');
    if (token) config.headers['Authorization'] = 'Bearer ' + localStorage.getItem("token")
    return config;
})



axiosInstance.interceptors.response.use(
    (response) => {
        try {
            document.getElementById('spinner').classList.add('hide');
        } catch (e) { }
        return response.data
    }, error => {

        try {
            document.getElementById('spinner').classList.add('hide');
        } catch (e) { }

        if (localStorage.getItem('skipAuthError') === 'true') {
            localStorage.removeItem('skipAuthError')
            if (error.response && error.response.data) {
                error.response.data.status = error.response.status;
                throw error.response.data;
            }
            return Promise.reject(error);
        }
        if (error.response && error.response.status === 401) {
            localStorage.removeItem("token");
            window.location.href = "/login";
        }
        if (error.response && error.response.data) {
            error.response.data.status = error.response.status;
            throw error.response.data;
        }

        return Promise.reject(error);
    });

export default axiosInstance;

