import React, { useEffect } from 'react'

export default function TopItems({ left, title, right = null }) {
    useEffect(() => {
        document.title = title
    }, [title])
    return (
        <div className="home-top-bar">
            {left && left}
            <div style={{ flex: 1 ,justifyContent:'center',display:'flex'}} className="logo">
                <h4>{title}</h4>
            </div>
            {right && right}
        </div>

    )
}
