import { IconButton, InputAdornment, TextField } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { FaTimes, FaUserAlt } from "react-icons/fa";
import { RiLockPasswordFill } from "react-icons/ri";
import { useHistory } from "react-router";
import { AiFillEye, AiFillEyeInvisible } from "react-icons/ai";
import api from './Api'
import { Context } from "./Context";
import { toast } from 'react-toastify'
export default function Login() {
  const context = React.useContext(Context)
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showInput, setShowInput] = useState(false);
  useEffect(() => {
    //parse query
    const query = new URLSearchParams(window.location.search);
    const text = query.get("text");
    if (text) {
      toast.success(text)
    }

  }, [])
  return (
    <div>
      <div className="login-background">
        <div className="custom-divider-top-rect">

          <img
            alt="logo"
            className="login-image"
            src="https://www.pinclipart.com/picdir/big/43-433889_matlab-kana-youtube-piotr-burnos-blog-naukowy-youtube.png"
          />
          <IconButton onClick={() => history.replace("/")} style={{ position: 'fixed', right: 0, top: 0 }}>
            <FaTimes />
          </IconButton>

        </div>
        <div className="custom-shape-divider-top">
          <svg
            data-name="Layer 1"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 1200 120"
            preserveAspectRatio="none"
          >
            <path
              d="M0,0V7.23C0,65.52,268.63,112.77,600,112.77S1200,65.52,1200,7.23V0Z"
              className="shape-fill"
            ></path>
          </svg>
        </div>
      </div>
      <div className="login-page">
        <div className="welcome-login">
          <p
            style={{
              fontWeight: 700,
              marginTop: 10,
              fontSize: 30,
              lineHeight: "22px",
            }}
          >
            Welcome !
          </p>
          <p
            style={{
              fontWeight: 700,
              marginTop: 10,
              fontSize: 20,
              lineHeight: "22px",
            }}
          >
            Sign In to continue
          </p>
        </div>
        <div className="username">
          <form onSubmit={e => {
            e.preventDefault();
            localStorage.setItem('skipAuthError', true)
            api.post("users/login", { email, password }).then(res => {
              localStorage.setItem("token", res.token);
              localStorage.setItem("user", JSON.stringify(res.user));
              context.setState({ user: res.user });
              toast.success("Login Successful.")
              history.replace("/");
            })
              .catch(err => {
                toast.error("Username/Password not matched")
              })
          }}>

            <TextField
              value={email}
              onChange={e => setEmail(e.target.value)}
              placeholder="Email"
              className="search-bar"
              style={{
                backgroundColor: "white",
                borderRadius: 10,
                marginTop: "5px",
                marginBottom: "20px",
              }}
              InputProps={{
                type: 'email',
                required: true,
                disableUnderline: true,
                startAdornment: (
                  <InputAdornment position="start">
                    <FaUserAlt style={{ margin: "8px" }} size={20} />
                  </InputAdornment>
                ),
              }}
            />
            <TextField
              value={password}
              onChange={e => setPassword(e.target.value)}
              type={showInput ? "text" : "password"}
              placeholder="Password "
              className="search-bar"
              style={{
                backgroundColor: "white",
                borderRadius: 10,
                marginTop: "5px",
                marginBottom: "5px",
              }}
              InputProps={{
                required: true,
                disableUnderline: true,
                startAdornment: (
                  <InputAdornment position="start">
                    <RiLockPasswordFill style={{ margin: "8px" }} size={20} />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment position="start">
                    {showInput ? (
                      <div onClick={() => setShowInput(false)}>
                        <AiFillEyeInvisible style={{ margin: "8px" }} size={20} />
                      </div>
                    ) : (
                      <div onClick={() => setShowInput(true)}>
                        <AiFillEye style={{ margin: "8px" }} size={20} />
                      </div>
                    )}
                  </InputAdornment>
                ),
              }}
            />
            <p
              onClick={() => history.push("/forgotpassword")}
              style={{
                fontWeight: 400,
                marginTop: 10,

                fontSize: 12,
                lineHeight: "22px",
              }}
            >
              Forgot Password? Click here
            </p>

            <button type="submit" className="sign-in">Sign In</button>
          </form>
          <div
            style={{
              fontWeight: 400,
              marginTop: 10,

              fontSize: 12,
              lineHeight: "22px",
            }}
          >
            <div onClick={() => history.push("/register")} style={{ display: 'flex' }}>
              New User?{" "}
              <div style={{ marginLeft: 7, color: 'blue' }}>
                Sign Up from here
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
