import React, { useEffect } from "react";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdStar } from "react-icons/md";
import {
  RiHeart2Fill,
  RiQuestionnaireFill,
} from "react-icons/ri";
import { useHistory } from "react-router";
import { IoMdShare } from "react-icons/io";
import api from './Api'
import Constants from '../Constant'
import { Context } from './Context'
import Carousel from "react-bootstrap/Carousel";
import Review from "./Review";
import NothingFound from "./NothingFound";
import Question from "./Question";
import parse from 'html-react-parser';
export default function ProductDetails() {
  const history = useHistory();
  const context = React.useContext(Context)
  const [product, setProduct] = React.useState({});
  const [questions, setQuestions] = React.useState([]);
  const [reviews, setReviews] = React.useState([]);
  useEffect(() => {
    api.get('/products/' + window.location.pathname.split('/').pop())
      .then(product => {
        document.title = "Product Detail: " + product.name;
        setQuestions(product.questions);
        setReviews(product.reviews);
        product.images = JSON.parse(product.images).map(image => Constants.IMAGE_BASE + image)
        return product;
      })
      .then(res => setProduct(res))
  }, [])
  return (
    <div className="home-main">
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black", padding: 1, background: 'gray', borderRadius: 10 }}
          size={25}
        />

        <RiHeart2Fill style={{ color: "black", padding: 1, background: 'gray', borderRadius: 10 }} size={25} />
      </div>
      <div className="details-image-div">
        <Carousel nextIcon={null} prevIcon={null} >
          {product.images && product.images.map((image, index) => (
            <Carousel.Item key={index}>
              <img
                src={image}
                alt="product"
                className="details-image"
              />
            </Carousel.Item>
          ))}
        </Carousel>
      </div>
      <div className="details-data">
        <div className="space-btwn">
          <p
            style={{
              margin: 5,
              fontSize: 25,
              color: "black",
              fontWeight: 800,
              position: "relative",
            }}
          >
            {product.name}
          </p>
          <IoMdShare style={{ color: "black" }} size={25} />
        </div>
        <div>
          <div
            style={{
              margin: "5px",
              fontWeight: 500,
              fontSize: 23,
              display: "flex",
              alignItems: "center",
            }}
          >
            {context.state.currency}{product.selling_price}
            <s style={{ fontSize: "15px", fontWeight: 400, marginLeft: "5px" }}>
              {context.state.currency}{product.mark_price}
            </s>
            <div
              style={{
                backgroundColor: "orange",
                color: "white",
                fontSize: 15,
                marginLeft: 10,
                padding: "0px 2px 0px 2px",
                borderRadius: 3,
              }}
            >
              <MdStar size={16} />
              {product.review || 0}
            </div>
          </div>
          <div className="star"></div>
        </div>
        <div className="rating-review">
          <div className="space-btwn">
            <p
              style={{
                margin: 0,
                fontSize: 15,
                color: "black",
                fontWeight: 500,
                position: "relative",
              }}
            >
              Ratings and Reviews
            </p>
            <i style={{ color: "red" }} onClick={() => history.push("/review/" + product.id)}>view all</i>
          </div>
          <br />
          {reviews.map((review, index) => (
            <Review key={index} review={review} />
          ))}
          {reviews.length === 0 && <NothingFound image={<MdStar size="30" />} data={"No Reviews yet."} />}

          <div
            style={{
              display: "flex",
              justifyContent: "center",
              marginTop: "30px",
            }}
          >
            <button className="sign-in" onClick={() => history.push("/review/" + product.id)}>Add a review</button>
          </div>
        </div>
        <div className="rating-review">
          <div className="space-btwn">
            <p
              style={{
                margin: 0,
                fontSize: 15,
                color: "black",
                fontWeight: 500,
                position: "relative",
              }}
            >
              Questions about this product
            </p>
            <i style={{ color: "red" }} onClick={() => history.push("/questions/" + product.id)}>view all</i>
          </div>
          <div className="question-answer">
            {questions.map((question, index) => (
              <Question key={index} question={question} />
            ))}
            {questions.length === 0 && <NothingFound image={<RiQuestionnaireFill size="30" />} data={"No Questions yet. "} />}
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginTop: "30px",
              }}
            >
              <button className="sign-in" onClick={() => history.push("/questions/" + product.id)}>Ask your question</button>
            </div>
          </div>
        </div>
        { <div className="rating-review" style={{ paddingBottom: "25px" }}>
          <p
            style={{
              margin: "0px 0px 10px 0px",
              fontSize: 15,
              color: "black",
              fontWeight: 500,
              position: "relative",
            }}
          >
            Description
          </p>
          <ul style={{ fontSize: 13 }}>
            {parse(product.desc||"")}
          </ul>
        </div>}
      </div>
    </div>
  );
}
