import React, { useEffect } from "react";
import { IoMdArrowRoundBack } from "react-icons/io";
import { useHistory } from "react-router";
import { BsExclamation } from "react-icons/bs";
import { NativeSelect } from "@material-ui/core";
import api from './Api'
import NothingFound from "./NothingFound";
import Order from "./Order";
export default function Address() {
  const history = useHistory();
  const [orders, setOrders] = React.useState([]);
  const fetchOrders = (url = "orders") => {
    api.get(url+"?time="+Date.now()).then(res => {
      setOrders(res.data)
    })
  }
  useEffect(fetchOrders, []);
  return (
    <div className="home-main">
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>My Orders</h4>
        </div>
        <BsExclamation
          style={{ color: "black", visibility: "hidden" }}
          size={35}
        />
      </div>
      <div className="form-group">
        <label >Order Type</label>
        <NativeSelect
          onChange={(e) => {
            if (e.target.value === 5) {
              fetchOrders()
            } else fetchOrders("orders/type/" + e.target.value)
          }}
          defaultValue={5}
          inputProps={{
            name: "age",
            id: "uncontrolled-native",
          }}
        >
          {Object.values(selectList).map((x, i) => (
            <option value={i}>{x}</option>
          ))}
        </NativeSelect>
      </div>
      <div className="order-selection">

        {orders.length > 0 ? orders.map((order) => <Order onClick={e => history.push("/orders/" + order.id)} order={order} />) : <NothingFound data={"No orders yet. Make one and it will appear here"} />}
      </div>
    </div>
  );
}
const selectList = {
  0: "In Review",
  1: "Approved",
  2: "Shipped",
  3: "Canceled",
  4: "Delivered",
  5: "All"
};
