import React, { useEffect } from 'react';
import api from './Api'
import Constants from '../Constant';
import { Context } from './Context';
export default function Splash(props) {
    const context = React.useContext(Context)
    
  

    useEffect(() => {
        api.get('configs').then(configs => {
            if (configs) localStorage.setItem("configs", JSON.stringify(configs))
            document.documentElement.style.setProperty('--primary-color', configs.primary_color);
            document.documentElement.style.setProperty('--secondary-color', configs.secondary_color);
            context.setState(configs)
        })
        document.documentElement.style.setProperty('--primary-color', context.state.primary_color);
        document.documentElement.style.setProperty('--secondary-color', context.state.secondary_color);
        // eslint-disable-next-line
    }, [])
    return (
        <>
            <div id="spinner" className="hide" onClick={null} style={{ background: '#000', display: 'flex', justifyContent: 'center', alignItems: 'center', position: 'fixed', opacity: '.4', width: '100%', height: '100%', zIndex: 9999 }}>
                {context.state.loader ? <div>
                    <img className="rotate" src={Constants.IMAGE_BASE + context.state.loader} alt="loader" />
                </div> : <div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>}
            </div>
            {props.children}
        </>
    );
}