import React from 'react'
import { ImNotification } from 'react-icons/im'

export default function NothingFound({ data = null, image = null }) {
    return (
        <div className="card" style={{ padding: 7, margin: 5, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            {image ? image : <ImNotification style={{ marginTop: 7 }} size={25} />}
            <br />
            <h6 className="card-header">{data || "No Item Found"}</h6>
        </div>
    )
}
