import React, {  useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { RiHomeSmile2Fill, RiSearch2Fill } from 'react-icons/ri'
import { MdShoppingCart } from 'react-icons/md'
import { FaUserAlt } from 'react-icons/fa'
import { Context } from './Context';
import { Badge } from '@material-ui/core'
const paths = {
    '/home': 0,
    '/search': 1,
    '/cart': 2,
    '/account': 3
}
export default function BottomNavigation() {
    const context = React.useContext(Context)  
    const history = useHistory();
    const location = useLocation();
    const [path, setPath] = React.useState(0);
    useEffect(() => setPath(paths[window.location.pathname]), [location]);

    return (

        <div className="bottom-nav">
            <RiHomeSmile2Fill style={{ color: path === 0 ? "var(--secondary-color)" : 'black' }} size={25} onClick={() => { history.push('/home'); }} />
            <RiSearch2Fill style={{ color: path === 1 ? "var(--secondary-color)" : 'black' }} size={25} onClick={() => { history.push('/search'); }} />
            
            <Badge badgeContent={context.state.cart.length} color="secondary">
            <MdShoppingCart style={{ color: path === 2 ? "var(--secondary-color)" : 'black' }} size={25} onClick={() => history.push('/cart')} />
            </Badge>
            <FaUserAlt style={{ color: path === 3 ? "var(--secondary-color)" : 'black' }} size={20} onClick={() => history.push('/account')} />



        </div>
    )
}
