import React, { useEffect } from "react";
import { GoPlus } from "react-icons/go";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdDelete, MdLocationOn } from "react-icons/md";
import { useHistory } from "react-router";
import api from "./Api";
import NothingFound from "./NothingFound";
import { GrCheckboxSelected } from "react-icons/gr";
import { RiDeleteBinFill } from "react-icons/ri";
import { FaEdit } from "react-icons/fa";
import { Modal, Button } from "react-bootstrap";
import { toast } from "react-toastify";
export default function Address() {
  const history = useHistory();
  const [address, setAddress] = React.useState([]);
  const [billingName, setBillingName] = React.useState("");
  const [billingAddress, setBillingAddress] = React.useState("");
  const [billingZip, setBillingZip] = React.useState("");
  const [billingPhone, setBillingPhone] = React.useState("");
  const form = React.useRef();
  const [show, setShow] = React.useState(false);
  const [id, setId] = React.useState("");
  const [refresh, setRefresh] = React.useState(false);
  useEffect(() => {
    api.get(`/address?time=${new Date().getTime()}`).then((res) => {
      setAddress(res.data);
    });
  }, [refresh]);
  return (
    <div className="home-main">
      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>Address Book</h4>
        </div>
        <MdDelete style={{ opacity: 0 }} size={25} />
      </div>
      <div
        onClick={(e) => {
          setId(null);
          setShow(true);
        }}
        className="add-address"
      >
        <GoPlus />
        &nbsp; Add New Address
      </div>
      <div className="cart-list">
        {address.length > 0 ? (
          address.map((item) => (
            <div
              key={item.id}
              style={{ position: "relative" }}
              className="cart-item">
              <div style={{flexDirection:"column"}}>
              <div style={{ display: "flex" }}>
                <div className="address-book-icon">
                  <MdLocationOn />
                </div>
                <div style={{ flex: 1 }} className="address-book-details">
                  <div className="address-book-details-name">
                    <b>{item.name}</b>
                  </div>
                  <div className="address-book-details-phone">
                    <b>{item.phone}</b>
                  </div>
                  <div className="address-book-details-location">
                    {item.zip},{item.address}
                  </div>
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-around",
                  alignItems: "center",
                  borderTop:"1px solid gray"
                }}
              >
                <div
                  style={{ fontSize: 14 }}
                  onClick={(e) => {
                    //confirm
                    if (
                      window.confirm(
                        "Are you sure you want to make this default address?"
                      )
                    ) {
                      api
                        .post("users/update-profile", {
                          default_address: item.id,
                        })
                        .then((res) => {
                          toast.success("Address set as default.");
                        });
                    }
                    // <GrCheckbox size={17} />
                  }}
                  className="address-book-action "
                >
                  <GrCheckboxSelected size={17} />
                  &nbsp;Default{" "}
                </div>
                <div
                  style={{ flexWrap: "wrap", fontSize: 14 }}
                  onClick={(e) => {
                    setId(item.id);
                    setBillingName(item.name);
                    setBillingAddress(item.address);
                    setBillingPhone(item.phone);
                    setBillingZip(item.zip);
                    setShow(true);
                  }}
                  className="address-book-action"
                >
                  <FaEdit size={17} />
                  &nbsp;
                  Edit
                </div>

                <div
                  style={{ fontSize: 14 }}
                  onClick={(e) => {
                    //confirm
                    if (
                      window.confirm(
                        "Are you sure you want to delete this address?"
                      )
                    ) {
                      api
                        .delete("/address/" + item.id)
                        .then((res) => setRefresh(!refresh));
                    }
                  }}
                  className="address-book-action"
                >
                  <RiDeleteBinFill size={17} />
                  &nbsp;
                  Delete{" "}
                </div>
              </div>
              </div>
            </div>
          ))
        ) : (
          <NothingFound data="No addresses yet." />
        )}
      </div>

      <Modal
        dialogClassName="modal-90w"
        show={show}
        onHide={() => setShow(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title>{id ? "Edit" : "Add"} a address</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form
            onSubmit={(e) => {
              e.preventDefault();
              api
                .post(id ? `/address/${id}` : "/address", {
                  name: billingName,
                  address: billingAddress,
                  zip: billingZip,
                  phone: billingPhone,
                })
                .then((d) => {
                  setShow(false);
                  // if (d.address) {
                  // setAddress(address.map(item => item.id === d.address.id ? d.address : item))
                  // }
                  setRefresh(!refresh);
                });
            }}
          >
            <div className="form-group">
              <label>Billing Full Name</label>
              <input
                pattern="[a-zA-z]+ [a-zA-z]+[ a-zA-z]*"
                title="Please enter a valid name"
                required
                value={billingName}
                onChange={(e) => setBillingName(e.target.value)}
                type="text"
                className="form-control"
              />
            </div>
            <div className="form-group">
              <label>Billing Address</label>
              <input
                minLength={10}
                required
                value={billingAddress}
                onChange={(e) => setBillingAddress(e.target.value)}
                type="text"
                className="form-control"
              />
            </div>
            <div className="form-group">
              <label>Billing Zip</label>
              <input
                required
                value={billingZip}
                onChange={(e) => setBillingZip(e.target.value)}
                type="number"
                className="form-control"
              />
            </div>
            <div className="form-group">
              <label>Billing Phone</label>
              <input
                required
                value={billingPhone}
                onChange={(e) => setBillingPhone(e.target.value)}
                type="phone"
                className="form-control"
              />
            </div>
            <button style={{ display: "none" }} ref={form}>
              Submit
            </button>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShow(false)}>
            Close
          </Button>
          <Button variant="primary" onClick={() => form.current.click()}>
            {id ? "Edit" : "Add"} Address
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
