import { NativeSelect } from "@material-ui/core";
import React, { useEffect } from "react";
import { BsPersonFill } from "react-icons/bs";
import { IoMdArrowRoundBack } from "react-icons/io";
import { MdDelete, MdEmail, MdLocationCity, MdLocationOn, MdPhone } from "react-icons/md";
import { RiSecurePaymentFill } from "react-icons/ri";
import { useHistory } from "react-router";
import { GrDocumentZip } from "react-icons/gr";
import api from './Api';
import { toast } from 'react-toastify';
import { Context } from './Context'
import { FaStickyNote } from "react-icons/fa";
import Constants from '../Constant'
const DEFAULT = "Note for seller(leave if not required)";
const selectList = {
  0: "In Review",
  1: "Approved",
  2: "Shipped",
  3: "Canceled",
  4: "Delivered",
  5: "All"
};

export default function ViewOrder() {
  const context = React.useContext(Context);
  const history = useHistory();
  const [isSubmitted, setIsSubmitted] = React.useState(true);
  const [billingName, setBillingName] = React.useState("");
  const [billingEmail, setBillingEmail] = React.useState("");
  const [billingPhone, setBillingPhone] = React.useState("");
  const [billingAddress, setBillingAddress] = React.useState("");
  const [billingZip, setBillingZip] = React.useState("");
  const [additionalNotes, setAdditionalNotes] = React.useState(DEFAULT);
  const [additionalAddress, setAdditionalAddress] = React.useState("");
  const [coupon, setCoupon] = React.useState("");
  const [shippings, setShippings] = React.useState([]);
  const [shippingId, setShippingId] = React.useState("");
  const [responseProducts, setResponseProducts] = React.useState([]);
  const [totalPrice, setTotalPrice] = React.useState(0);
  const [totalShipping, setTotalShipping] = React.useState(0);
  const [totalDiscount, setTotalDiscount] = React.useState(0);
  const [totalTax, setTotalTax] = React.useState(0);
  const [subTotal, setSubTotal] = React.useState(0);
  const [orderId, setOrderId] = React.useState("");
  const [isApproved, setIsApproved] = React.useState(false);
  const [status, setStatus] = React.useState(0);
  useEffect(() => {
    api.get("shippings").then(shippings => {
      setShippings(shippings.data);
      api.get("orders/" + window.location.pathname.split("/")[2]).then(res => {
        setIsSubmitted(true);
        setTotalPrice(res.billing_total);
        setTotalShipping(res.shipping_total);
        setTotalDiscount(res.discount);
        setTotalTax(res.billing_tax);
        setSubTotal(res.billing_subtotal);
        setOrderId(res.id);
        setResponseProducts(res.products.map(item => ({
          name:item.name,
          quantity:item.pivot.quantity,
          selling_price:item.pivot.price,
          total:item.pivot.price*item.pivot.quantity,
          images: JSON.parse(item.images)
        })));
        setStatus(res.status);
        setBillingEmail(res.billing_email);
        setBillingName(res.billing_name);
        setBillingPhone(res.billing_phone);
        setBillingAddress(res.billing_address);
        setBillingZip(res.billing_zip);
        setAdditionalAddress(res.additional_address);
        setAdditionalNotes(res.order_desc);
        setShippingId(res.shipping_id);
        setIsApproved(res.is_approved);

      }).catch(err => {
        if (err.error) {
          toast.error(err.error);
          history.goBack();
        }
      })


    })
    // eslint-disable-next-line 
  }, [])
  return (
    <div className="home-main">

      <div className="home-top-bar">
        <IoMdArrowRoundBack
          onClick={() => history.goBack()}
          style={{ color: "black" }}
          size={25}
        />
        <div className="logo">
          <h4>Order Details</h4>
        </div>
        <MdDelete style={{ color: "black", visibility: "hidden" }} />
      </div>
      <div className="checkout-details">
        <div className="billing-location">
          <MdLocationCity />
          <label
            for="fruit"
            style={{ marginLeft: "10px", marginRight: "10px" }}
          >
            Shipping Region
          </label>
          <NativeSelect disabled={isSubmitted} fullWidth value={shippingId} onChange={e => setShippingId(e.target.value)}>
            {shippings.map((x) => (
              <option value={x.id}>{x.name}</option>
            ))}
          </NativeSelect>
        </div>
        <div className="checkout-name">
          <BsPersonFill />
          <input disabled={isSubmitted} required className="form-control ml-10" value={billingName} onChange={(e) => setBillingName(e.target.value)} />

        </div>
        <div className="checkout-name">
          <MdLocationOn />
          <textarea disabled={isSubmitted} required onChange={(e) => setBillingAddress(e.target.value)}
            multiple="true" className="form-control ml-10" value={billingAddress} >
          </textarea>

        </div>
        <div className="checkout-name">
          <MdLocationOn />
          <textarea disabled={isSubmitted} required placeholder="Additional Address/Landmarks" onChange={(e) => setAdditionalAddress(e.target.value)}
            multiple="true" className="form-control ml-10" value={additionalAddress} >
          </textarea>
        </div>
        <div className="checkout-name">
          <GrDocumentZip />
          <input disabled={isSubmitted} required className="form-control ml-10" value={billingZip} onChange={(e) => setBillingZip(e.target.value)} />
        </div>
        <div className="checkout-name">
          <MdPhone />
          <input disabled={isSubmitted} required className="form-control ml-10" value={billingPhone} onChange={(e) => setBillingPhone(e.target.value)} />
        </div>
        <div className="checkout-name">
          <MdEmail />
          <input disabled={isSubmitted} required className="form-control ml-10" value={billingEmail} onChange={(e) => setBillingEmail(e.target.value)} />
        </div>
        <div className="checkout-name">
          <FaStickyNote />
          <textarea disabled={isSubmitted} placeholder="Note for seller" onChange={(e) => setAdditionalNotes(e.target.value)}
            multiple="true" className="form-control ml-10" value={additionalNotes} >
          </textarea>
        </div>
        <div className="checkout-name">
          <RiSecurePaymentFill />
          <input disabled={isSubmitted} placeholder="Coupon code (if any)" className="form-control ml-10" value={coupon} onChange={(e) => setCoupon(e.target.value)} />
        </div>
      </div>



      <br />
      <h6><u>Products</u></h6>




      <div style={{ background: 'rgb(234 236 239)' }} className="checkout-list">
        {responseProducts.map((item, index) => (
          <div key={index} className="checkout-item">
            <div className="checkout-item-top">
              <div>
                <img
                  src={Constants.IMAGE_BASE + item.images[0]}
                  className="checkout-image"
                  alt=""
                ></img>
              </div>
              <div className="checkout-data">
                <b>{item.name}</b> x {item.quantity}
                <p style={{ margin: 0, fontSize: 12, color: "black" }}>
                  {context.state.currency}{item.selling_price}
                </p>
              </div>
              <div className="checkout-data-price">
                <b>total {context.state.currency}{item.total}</b>
              </div>
            </div>



          </div>))}


      </div>


      <div style={{ marginBottom: 10, padding: 10, background: 'rgb(234 236 239)' }}>
        <div style={{ display: 'flex' }}>
          <div style={{ flex: 1, marginRight: 10 }} className="checkout-item-middle">
            Shipping fee: {context.state.currency}{totalShipping}
            <p style={{ margin: 0 }}>
              TAX: {context.state.currency}{totalTax}
            </p>
            <p style={{ margin: 0 }}>
              Discount: {context.state.currency}{totalDiscount}
            </p>

          </div>

          <div className="proceed">
            <p style={{ margin: 0 }}>
              Sub Total: {context.state.currency}{subTotal}
            </p>
            <b>Total: {context.state.currency}{totalPrice}</b>

          </div>
        </div>
        {isApproved ? <div>
          <hr />
          <div style={{ display: 'flex', justifyContent: 'center' }}>

            Order Status: <b>{selectList[status]}</b>
          </div>
        </div> : <div>
          <hr />
          <small style={{ color: 'red' }}><p>By creating this order, you accept that you have read all the details properly and  accepted the Privacy and Terms and Conditions.</p>
          </small>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <button onClick={() => {
              if (window.confirm('Are you sure to proceed?')) {
                api.get("orders/cancel/" + orderId).then(res => {
                  toast.success("Order Cancelled.")
                  history.goBack();
                })
              }
            }} type="button" className="btn btn-danger">Cancel</button>
            &nbsp;&nbsp;
            <button onClick={() => {
              if (window.confirm('Are you sure to proceed?')) {
                api.get("orders/approve/" + orderId).then(res => {
                  toast.success("Order created successfully.");
                  history.goBack();
                })
              }
            }} type="button" className="btn btn-primary">Create Order</button>
          </div>
        </div>}



      </div>

      <br />

    </div>
  );
}

