import React from 'react'
import { RiQuestionnaireFill } from "react-icons/ri";
import TimeAgo from 'react-timeago'
export default function Question({ question }) {
    return (
        <div className="question-answer">
            <div
                style={{
                    display: "flex",
                    alignItems: "flex-start",
                    marginBottom: "3px",
                }}
            >
                <RiQuestionnaireFill style={{ marginRight: "10px" }} />
                <div>
                    <p style={{ margin: 0, fontSize: "13px" }}>
                        Q.{question.question}
                    </p>
                    {question.answer && <p style={{ margin: 0, fontSize: "13px" }}>
                        A.{question.answer}
                    </p>}
                    <p
                        style={{
                            marginBottom: 0,
                            marginRight: "15px",
                            fontSize: 11,
                            color: "gray",
                            fontWeight: 400,
                            position: "relative",
                        }}
                    >
                        {question.user_name}.<TimeAgo date={question.created_at} />
                    </p>
                </div>
            </div>

        </div>
    )
}
